  letter-spacing: -0.01em;
}
.md-typeset h5,
.md-typeset h6 {
  margin: 1.25em 0;
  color: var(--md-default-fg-color--light);
  font-weight: 700;
  font-size: 0.8em;
  letter-spacing: -0.01em;
