}
.md-typeset pre {
  position: relative;
  display: flow-root;
  margin: 1em 0;
  line-height: 1.4;
}
.md-typeset pre > code {
  display: block;
  margin: 0;
  padding: 0.7720588235em 1.1764705882em;
  overflow: auto;
  word-break: normal;
  box-shadow: none;
  box-decoration-break: slice;
  touch-action: auto;
  scrollbar-width: thin;
  scrollbar-color: var(--md-default-fg-color--lighter) transparent;
}
.md-typeset pre > code:hover {
  scrollbar-color: var(--md-accent-fg-color) transparent;
}
.md-typeset pre > code::-webkit-scrollbar {
  width: 0.2rem;
  height: 0.2rem;
}
.md-typeset pre > code::-webkit-scrollbar-thumb {
  background-color: var(--md-default-fg-color--lighter);
}
.md-typeset pre > code::-webkit-scrollbar-thumb:hover {
  background-color: var(--md-accent-fg-color);
}
@media screen and (max-width: 44.9375em) {
  .md-typeset > pre {
    margin: 1em -0.8rem;
  }
  .md-typeset > pre code {
    border-radius: 0;
  }
}
.md-typeset kbd {
  display: inline-block;
  padding: 0 0.6666666667em;
  color: var(--md-default-fg-color);
  font-size: 0.75em;
  vertical-align: text-top;
  word-break: break-word;
  background-color: var(--md-typeset-kbd-color);
  border-radius: 0.1rem;
  box-shadow: 0 0.1rem 0 0.05rem var(--md-typeset-kbd-border-color), 0 0.1rem 0 var(--md-typeset-kbd-border-color), 0 -0.1rem 0.2rem var(--md-typeset-kbd-accent-color) inset;
}
.md-typeset mark {
  color: inherit;
  word-break: break-word;
  background-color: var(--md-typeset-mark-color);
  box-decoration-break: clone;
}
.md-typeset abbr {
  text-decoration: none;
  border-bottom: 0.05rem dotted var(--md-default-fg-color--light);
  cursor: help;
}
@media (hover: none) {
  .md-typeset abbr {
    position: relative;
  }
  .md-typeset abbr[title]:focus::after, .md-typeset abbr[title]:hover::after {
    box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 1px 5px 0 rgba(0, 0, 0, 0.12), 0 3px 1px -2px rgba(0, 0, 0, 0.2);
    position: absolute;
    left: 0;
    display: inline-block;
    width: auto;
    min-width: max-content;
    max-width: 80%;
    margin-top: 2em;
    padding: 0.2rem 0.3rem;
    color: var(--md-default-bg-color);
    font-size: 0.7rem;
    background-color: var(--md-default-fg-color);
    border-radius: 0.1rem;
    content: attr(title);
  }
}
.md-typeset small {
  opacity: 0.75;
}
.md-typeset sup,
.md-typeset sub {
  margin-left: 0.078125em;
}
[dir=rtl] .md-typeset sup,
[dir=rtl] .md-typeset sub {
  margin-right: 0.078125em;
  margin-left: initial;
}
.md-typeset blockquote {
  padding-left: 0.6rem;
