}
.md-typeset ul ol ol,
.md-typeset ol ol ol {
  list-style-type: lower-roman;
}
.md-typeset ul li,
.md-typeset ol li {
  margin-bottom: 0.5em;
  margin-left: 1.25em;
}
[dir=rtl] .md-typeset ul li,
[dir=rtl] .md-typeset ol li {
  margin-right: 1.25em;
  margin-left: initial;
}
.md-typeset ul li p,
.md-typeset ul li blockquote,
.md-typeset ol li p,
.md-typeset ol li blockquote {
  margin: 0.5em 0;
}
.md-typeset ul li:last-child,
.md-typeset ol li:last-child {
  margin-bottom: 0;
}
.md-typeset ul li ul,
.md-typeset ul li ol,
.md-typeset ol li ul,
.md-typeset ol li ol {
  margin: 0.5em 0 0.5em 0.625em;
}
[dir=rtl] .md-typeset ul li ul,
[dir=rtl] .md-typeset ul li ol,
[dir=rtl] .md-typeset ol li ul,
[dir=rtl] .md-typeset ol li ol {
  margin-right: 0.625em;
  margin-left: initial;
}
.md-typeset dd {
  margin: 1em 0 1.5em 1.875em;
}
[dir=rtl] .md-typeset dd {
  margin-right: 1.875em;
  margin-left: initial;
}
.md-typeset img,
.md-typeset svg {
  max-width: 100%;
  height: auto;
}
.md-typeset img[align=left],
.md-typeset svg[align=left] {
  margin: 1em;
  margin-left: 0;
}
.md-typeset img[align=right],
.md-typeset svg[align=right] {
  margin: 1em;
  margin-right: 0;
}
.md-typeset img[align]:only-child,
.md-typeset svg[align]:only-child {
  margin-top: 0;
}
.md-typeset figure {
  width: fit-content;
  max-width: 100%;
  margin: 0 auto;
  text-align: center;
}
.md-typeset figure img {
  display: block;
}
.md-typeset figcaption {
  max-width: 24rem;
  margin: 1em auto 2em;
  font-style: italic;
}
.md-typeset iframe {
  max-width: 100%;
}
.md-typeset table:not([class]) {
  display: inline-block;
  max-width: 100%;
  overflow: auto;
  font-size: 0.64rem;
  background-color: var(--md-default-bg-color);
  border-radius: 0.1rem;
  box-shadow: 0 0.2rem 0.5rem rgba(0, 0, 0, 0.05), 0 0 0.05rem rgba(0, 0, 0, 0.1);
  touch-action: auto;
}
@media print {
  .md-typeset table:not([class]) {
    display: table;
  }
}
.md-typeset table:not([class]) + * {
  margin-top: 1.5em;
}
