}
.md-typeset table th[role=columnheader] {
  cursor: pointer;
}
.md-typeset table th[role=columnheader]::after {
  display: inline-block;
  width: 1.2em;
  height: 1.2em;
  margin-left: 0.5em;
  vertical-align: sub;
  mask-repeat: no-repeat;
  mask-size: contain;
  content: "";
}
